<template>
  <view class="call-view">
    <view class="nav-bar">
      <text class="duration">{{duration}}</text>
    </view>

    <!-- 音频通话 -->
    <view v-if="call.mediaType===0" class="profile">
      <image class="user-avatar" mode="widthFix" :src="friend.data.avatar"></image>
      <text class="user-name">{{friend.data.name}}</text>
    </view>

    <!-- 视频通话 -->
    <view v-if="call.mediaType === 1" class="content">
      <view class="container">
        <grtc-video :userId="mainVideoUserId" class="main-video"></grtc-video>
        <grtc-video @click="toggleUser()" :userId="miniVideoUserId" class="mini-video"></grtc-video>
      </view>
    </view>

    <view class="action-box">
      <view class="action-item" @click="end()">
        <view class="end-background">
          <image class="action-icon" src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/8af91edd-40d2-4bbb-8a5c-caef7457bb07.png"></image>
        </view>
        <text class="action-text">挂断</text>
      </view>
    </view>
  </view>
</template>

<script>
  const GRTC = uni.$GRTC;
  let interval = null;
  export default {
    data() {
      return {
        call: GRTC.currentCall(),
        friend: null,
        mainVideoUserId: null,
        miniVideoUserId: null,
        duration: null
      }
    },
    onLoad() {
      this.currentUser = uni.$currentUser;
      this.friend = this.currentUser.id === this.call.caller.id ? this.call.callees[0] : this.call.caller;
      this.miniVideoUserId = this.currentUser.id;
      this.mainVideoUserId = this.friend.id;

      this.duration = this.formattedDuration();
      interval = setInterval(() => {
        this.duration = this.formattedDuration();
      }, 1000);

      this.initListener();
    },
    onUnload() {
      GRTC.off(GRTC.EVENT.USER_QUIT, this.onUserQuit);
    },
    onBackPress(event) {
      return event.from === 'backbutton';  // 禁止安卓侧滑返回
    },
    methods: {
      initListener() {
        GRTC.on(GRTC.EVENT.USER_QUIT, this.onUserQuit);
      },
      onUserQuit(event) {
        clearInterval(interval);
        const username = event.user.id === this.currentUser.id ? '' : event.user.data.name;
        let message = '';
        switch (event.reason) {
          case 'GOEASY_DISCONNECTED':
            message = `${username}网络异常`;
            break;
          case 'HUNG_UP':
            message = `${username}已挂断`;
            break;
        }
        plus.nativeUI.toast(message);
        uni.navigateBack();
      },
      end() {
        GRTC.end();
      },
      toggleUser() {
        const temp = this.mainVideoUserId;
        this.mainVideoUserId = this.miniVideoUserId;
        this.miniVideoUserId = temp;
      },
      formattedDuration() {
        const seconds = this.call.getDuration();
        const minutes = Math.floor(seconds / 60);
        const formattedSeconds = String(seconds % 60).padStart(2, '0');
        const formattedMinutes = String(minutes).padStart(2, '0');
        return `${formattedMinutes}:${formattedSeconds}`; // mm:ss
      }
    }
  }
</script>

<style scoped>
  @import url('/static/style/rtcCallStyle.css');
</style>